import React from "react";
import { GetUserInformation } from "../Adapters/GetUserInformation";

const useGatherUserInformation = () => {
  const [userInformation, setUserInformation] = React.useState({});
  React.useEffect(() => {
    GetUserInformation(setUserInformation);
  }, []);
  return { userInformation };
};

export default useGatherUserInformation;
