import {
  Container,
  LinearProgress,
  Stack,
} from '@mui/material';
import SubmitButton from 'Global/Components/UnEditableComponents/SubmitButton/SubmitButton';
import ReactHookFormGenerator from 'Global/ReactHookFormGenerator/ReactHookFormGenerator';
import React, { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { HandleFormSubmit } from './Functions/HandleFormSubmit';
import useGatherUserInformation from './Hooks/useGatherUserInformation';
import { CompleteUserProfileDataArray } from './Variables/CompleteUserProfileDataArray';
import { CompleteUserProfileDefaultValues } from './Variables/CompleteUserProfileDefaultValues';
import { useTranslation } from 'react-i18next';
import { useContext } from 'react';
import { GlobalContext } from 'Global/Context/GlobalContextProvider';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import Cookies from 'js-cookie';
import useCompleteUserProfileDataArray from './Hooks/useCompleteUserProfileDataArray';
import useGetGenderList from '../../../Global/Adapters/useGetGenderList';
import useGetTaholList from '../../../Global/Adapters/useGetTaholList';
import useGetCityList from 'Global/Adapters/useGetCityList';
import useGetDefaultParamApp from 'Global/Adapters/useGetDefaultParamApp';
import { formatData } from './Functions/formatData';

const CompleteProfileUser = () => {

  const defaultParams = useGetDefaultParamApp();
  const cityList = useGetCityList();
  const genderList = useGetGenderList();
  const taholList = useGetTaholList();



  const { userInformation } = useGatherUserInformation();
  const { control, formState, handleSubmit } = useForm({
    defaultValues:
      CompleteUserProfileDefaultValues(userInformation),
  });
  const { t, i18n } = useTranslation();
  const languageArray =
    useContext(GlobalContext)?.LanguageArray;
  const roles = useContext(GlobalContext).roles;

  const user_cookie = JSON.parse(Cookies.get('user'));
  const enableEdit = user_cookie?.CommitedHisInfo;




  
  const dataArray = useCompleteUserProfileDataArray(
    userInformation,
    languageArray,
    i18n,
    enableEdit,
    cityList,
    genderList,taholList,defaultParams
  );

  const save_label = FindLangKey('general-public-btn-save');

   const userId = user_cookie?.Id

 

  return (
    <React.Fragment>
      <Container maxWidth="md">
        <form
          onSubmit={handleSubmit((data) => {
             

            formatData(cityList,genderList,data,user_cookie,userId,HandleFormSubmit,userInformation);
            
           
            HandleFormSubmit(data, userInformation);

         

          })}
        >
          <Stack
            marginTop="1em"
            sx={{
              gap: '.5em',
            }}
          >
            {dataArray ? (
              <ReactHookFormGenerator
                control={control}
                formState={formState}
                DataArray={dataArray}
              />
            ) : (
              <LinearProgress />
            )}
            {dataArray ? (
              <SubmitButton label={save_label} />
            ) : (
              <></>
            )}

            <br />
          </Stack>
        </form>
      </Container>
    </React.Fragment>
  );
};

export default CompleteProfileUser;
