import { SubmitUserInformation } from "../Adapters/SubmitUserInformation";

export const HandleFormSubmit = (data, profileInfo) => {
  for (let key in data) {
    if (data[key] === "" || data[key] === null || data[key] === undefined) {
      data[key] = profileInfo[key];
    }
  }
  SubmitUserInformation(data);
};
