import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import React, { useEffect, useState } from 'react'
import { Get } from 'Xhr/Xhr'

const useGetDefaultParamApp = () => {

    const[defaultParams,setDefaultParams] = useState();
 
    useEffect(()=>{
        Get(`${process.env.REACT_APP_API_URL_API_DOCTOR_GET_DEFAULT_PARAM_APP}`,{}).then((res)=>{
        
            // console.log('the res=>',res)

            if(res.data.resCode === 1){
                setDefaultParams(res.data.Data)
            }else{
                SwalAlert('error',res.data.resMessage,false);
            }
        
            
        })
      },[]);


      return defaultParams;
}

export default useGetDefaultParamApp