import ComponentMemoizer from 'Global/Components/ComponentMemoizer/ComponentMemoizer';
import EditableCustomizableAutoComplete from 'Global/Components/EditableComponents/EditableCustomizableAutoComplete/EditableCustomizableAutoComplete';
import EditableCustomizableSelect from 'Global/Components/EditableComponents/EditableCustomizableSelect/EditableCustomizableSelect';
import EditablePersianSupportTextField from 'Global/Components/EditableComponents/EditablePersianSupportTextField/EditablePersianSupportTextField';
import ReadOnlyTextField from 'Global/Components/ReadOnlyComponents/ReadOnlyTextField';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import Cookies from 'js-cookie';
import { ArabicToPersian } from 'utils/ConvertToPersianTextFromArabic';

export const CompleteUserProfileDataArray = (
  userInformation,
  languageArray,
  i18n,
  enableEdit,
  cityList
) => {
  // const user = JSON.parse(Cookies.get("user") || "");

  const completeUserProfileDataArray = [
    {
      element: (
        <EditablePersianSupportTextField
          disabled={enableEdit}
          label={FindLangKey(
            'complete-user-input-first-name',
            languageArray
          )}
          Initialvalue={ArabicToPersian(
            userInformation.FirstName
          )}
          isEnglish={i18n?.language === 'en'}
        />
      ),
      name: 'FirstName',
    },
    {
      element: (
        <EditablePersianSupportTextField
          disabled={enableEdit}
          label={FindLangKey(
            'complete-user-input-last-name',
            languageArray
          )}
          Initialvalue={ArabicToPersian(
            userInformation.LastName
          )}
          isEnglish={i18n?.language === 'en'}
        />
      ),
      name: 'LastName',
    },
    {
      element: (
        <EditablePersianSupportTextField
          disabled={enableEdit}
          label={FindLangKey(
            'complete-user-information-input-father-name',
            languageArray
          )}
          Initialvalue={ArabicToPersian(
            userInformation.FatherName
          )}
          isEnglish={i18n?.language === 'en'}
        />
      ),
      name: 'FatherName',
    },
    {
      element: (
        <EditablePersianSupportTextField
          disabled={enableEdit}
          label={FindLangKey(
            'general-public-mobile-number',
            languageArray
          )}
          Initialvalue={userInformation.Mobile}
          isEnglish={i18n?.language === 'en'}
        />
      ),
      name: 'Mobile',
    },
    {
      element: (
        <EditablePersianSupportTextField
          disabled={enableEdit}
          label={FindLangKey(
            'signup-input-email',
            languageArray
          )}
          Initialvalue={userInformation.Email}
          isEnglish={i18n?.language === 'en'}
        />
      ),
      name: 'Email',
    },
    {
      element: (
        <EditablePersianSupportTextField
          disabled={enableEdit}
          label={FindLangKey(
            'complete-user-information-input-birth-day',
            languageArray
          )}
          Initialvalue={userInformation.BirthDate}
          isEnglish={i18n?.language === 'en'}
        />
      ),
      name: 'BirthDate',
    },
    {
      element: (
        // <EditablePersianSupportTextField
        //   label={FindLangKey(
        //     "complete-user-information-input-city-born",
        //     languageArray
        //   )}
        //   Initialvalue={userInformation.nameCityTavalod}
        //   isEnglish={i18n?.language === "en"}
        // />
        <ComponentMemoizer
          element={
            <EditableCustomizableAutoComplete
              disabled={enableEdit}
              isSingle={true}
              label={FindLangKey(
                'complete-user-information-input-city-born'
              )}
              defaultValue={[
                userInformation.nameCityTavalod,
              ]}
              SelectableDataArray={cityList}
            />
          }
          dependecies={[userInformation.nameCityTavalod]}
        />
      ),
      name: 'nameCityTavalod',
    },
    {
      element: (
        // <EditablePersianSupportTextField
        //   label={FindLangKey(
        //     "complete-user-information-input-city-live",
        //     languageArray
        //   )}
        //   Initialvalue={userInformation.nameCitySokonat}
        //   isEnglish={i18n?.language === "en"}
        // />

        // <EditableCustomizableSelect
        //   disabled={true}
        //   isSingle={true}
        //   label={FindLangKey(
        //     "complete-user-information-input-city-live",
        //     languageArray
        //   )}
        //   initialData={[
        //     {
        //       ItemValue: userInformation.nameCitySokonat,
        //       ItemContent: userInformation.nameCitySokonat,
        //     },
        //   ]}
        //   SelectableDataArray={[
        //     {
        //       ItemValue: userInformation.idCitySokonat,
        //       ItemContent: userInformation.nameCitySokonat,
        //     },
        //   ]}
        // />
        <></>
      ),
      name: 'nameCitySokonat',
    },

    {
      element: (
        <EditablePersianSupportTextField
          label={FindLangKey(
            'general-public-address',
            languageArray
          )}
          Initialvalue={ArabicToPersian(
            userInformation.Address
          )}
          isEnglish={i18n?.language === 'en'}
        />
      ),
      name: 'Address',
      required: true,
    },
    {
      element: (
        <EditablePersianSupportTextField
          label={FindLangKey(
            'complete-user-information-input-telephone',
            languageArray
          )}
          Initialvalue={userInformation.Telephon}
          isEnglish={i18n?.language === 'en'}
        />
      ),
      name: 'Telephon',
      required: true,
    },
  ];
  return completeUserProfileDataArray;
};
