import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import React, { useEffect, useState } from 'react';
import { ArabicToPersian } from 'utils/ConvertToPersianTextFromArabic';
import { Post } from 'Xhr/Xhr';

const useGetCityList = () => {
  const [cityList, setCityList] = useState([]);

  useEffect(() => {
    Post(
      'Post',
      `${process.env.REACT_APP_API_URL_API_GET_CITY_LIST}`,
      true,
      {}
    ).then((r) => {
      if (r.data.resCode === 1) {
        setCityList(
          r.data.Data.list.map((el) => ({
            label: ArabicToPersian(el.Name),
            id: el.Id,
          }))
        );
      } else {
        SwalAlert('error', r.data.resMessage, false);
      }
    });
  }, []);

  return cityList;
};

export default useGetCityList;
