import EditableCustomizableAutoComplete from 'Global/Components/EditableComponents/EditableCustomizableAutoComplete/EditableCustomizableAutoComplete';
import EditableCustomizableSelect from 'Global/Components/EditableComponents/EditableCustomizableSelect/EditableCustomizableSelect';
import EditableMuiDatePicker from 'Global/Components/EditableComponents/EditableMuiDatePicker/EditableMuiDatePicker';
import EditablePersianSupportTextField from 'Global/Components/EditableComponents/EditablePersianSupportTextField';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import React, { useEffect, useState } from 'react';
import { ArabicToPersian } from 'utils/ConvertToPersianTextFromArabic';

const useCompleteUserProfileDataArray = (
  userInformation,
  languageArray,
  i18n,
  enableEdit,
  cityList,
  genderList,
  taholList,
  defaultParams
) => {
  const [dataArray, setDataArray] = useState();

  const fName_label = FindLangKey(
    'complete-user-input-first-name'
  );
  const lName_label = FindLangKey(
    'complete-user-input-last-name'
  );
  const fatherName_label = FindLangKey(
    'complete-user-information-input-father-name'
  );

  const mobileNumber = FindLangKey(
    'general-public-mobile-number'
  );
  const email_label = FindLangKey('signup-input-email');

  const bDay_label = FindLangKey(
    'complete-user-information-input-birth-day'
  );

  const city_born = FindLangKey(
    'complete-user-information-input-city-born'
  );

  const address_label = FindLangKey(
    'general-public-address'
  );

  const telNumber = FindLangKey(
    'complete-user-information-input-telephone'
  );

  const label_gender = FindLangKey(
    'general-public-word-gender'
  );
  const label_tahol = FindLangKey(
    'save-patients-for-visit-form-input-marriage-state'
  );

  // console.log('userInformation--->',userInformation)

  // console.log('--->', userInformation);

  useEffect(() => {
    if (
      cityList &&
      userInformation.nameCityTavalod &&
      genderList&&
      taholList
    ) {
      setDataArray([
        {
          element: (
            <EditablePersianSupportTextField
              disabled={enableEdit}
              label={fName_label}
              Initialvalue={ArabicToPersian(
                userInformation.FirstName
              )}
              isEnglish={i18n?.language === 'en'}
            />
          ),
          name: 'FirstName',
        },
        {
          element: (
            <EditablePersianSupportTextField
              disabled={enableEdit}
              label={lName_label}
              Initialvalue={ArabicToPersian(
                userInformation.LastName
              )}
              isEnglish={i18n?.language === 'en'}
            />
          ),
          name: 'LastName',
        },
    
        {
          element: (
            <EditablePersianSupportTextField
              disabled={enableEdit}
              label={fatherName_label}
              Initialvalue={ArabicToPersian(
                userInformation.FatherName
              )}
              isEnglish={i18n?.language === 'en'}
            />
          ),
          name: 'FatherName',
        },    {
          element: (
            <EditableCustomizableSelect
              disabled={enableEdit}
              isSingle={true}
              label={label_gender}
              initialData={[
                {
                  ItemValue: genderList?.filter(
                    (fl) =>
                      fl.ItemValue ===
                      userInformation.IdGender
                  )[0]?.ItemContent || "",
                  ItemContent: genderList?.filter(
                    (fl) =>
                      fl.ItemValue ===
                      userInformation.IdGender
                  )[0]?.ItemContent || "",
                },
              ]}
              SelectableDataArray={genderList}
              enableInitialOnchange={true}
            />
          ),
          name: 'IdGender',
          required: true,
        },
        {
          element: (
            <EditableCustomizableSelect
              isSingle={true}
              disabled={enableEdit}
              label={label_tahol}
              SelectableDataArray={taholList}
              isEnglish={i18n?.language === 'en'}
              initialData={[
                {
                  ItemContent:ArabicToPersian(userInformation.nameTaahol)||"",
                  ItemValue:ArabicToPersian(userInformation.nameTaahol)||""
                }
              ]}
              enableInitialOnchange={true}
            />
          ),
          name: 'idTaahol',
          required:true
        },
        {
          element: (
            <EditablePersianSupportTextField
              disabled={enableEdit}
              label={mobileNumber}
              Initialvalue={userInformation.Mobile}
              isEnglish={i18n?.language === 'en'}
            />
          ),
          name: 'Mobile',
        },
        {
          element: (
            <EditablePersianSupportTextField
              disabled={enableEdit}
              label={email_label}
              Initialvalue={userInformation.Email}
              isEnglish={i18n?.language === 'en'}
            />
          ),
          name: 'Email',
        },
        {
          element: (
            <EditableMuiDatePicker
            Disabled={enableEdit}
              Placeholder={bDay_label}
              initialValue={userInformation.BirthDate}
              isEnglish={i18n?.language === 'en'}
            />
          ),
          name: 'BirthDate',
        },
        {
          element: (
            // <EditablePersianSupportTextField
            //   label={FindLangKey(
            //     "complete-user-information-input-city-born",
            //     languageArray
            //   )}
            //   Initialvalue={userInformation.nameCityTavalod}
            //   isEnglish={i18n?.language === "en"}
            // />

            <EditableCustomizableAutoComplete
              disabled={enableEdit}
              isSingle={true}
              label={city_born}
              defaultValue={ArabicToPersian(
                userInformation.nameCityTavalod
               )||
             ArabicToPersian(cityList?.filter((fl)=>fl?.id === defaultParams?.idCityDifault)[0]?.label)
              
              }
              SelectableDataArray={cityList}
              initialOnChange={true}
            />
          ),
          name: 'nameCityTavalod',
        },
    
       

        {
          element: (
            <EditablePersianSupportTextField
              label={address_label}
              Initialvalue={ArabicToPersian(
                userInformation.Address
              )}
              isEnglish={i18n?.language === 'en'}
            />
          ),
          name: 'Address',
          required: true,
        },
        {
          element: (
            <EditablePersianSupportTextField
              label={telNumber}
              Initialvalue={userInformation.Telephon}
              isEnglish={i18n?.language === 'en'}
            />
          ),
          name: 'Telephon',
          required: true,
        },
      ]);
    }
  }, [
    userInformation,
    languageArray,
    i18n,
    enableEdit,
    cityList,
    genderList,
    taholList
  ]);

  return dataArray;
};

export default useCompleteUserProfileDataArray;
