import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import React, { useEffect, useState } from 'react';
import { ArabicToPersian } from 'utils/ConvertToPersianTextFromArabic';
import { Post } from 'Xhr/Xhr';

const useGetGenderList = () => {
  const [genderList, setGenderList] = useState([]);

  useEffect(() => {
    Post(
      'POST',
      `${process.env.REACT_APP_API_URL_API_GET_USER_GENDER_LIST}`,
      true,
      { Id: 0 }
    ).then((r) => {
      // console.log('rrr-->', r);

      if (r.data.resCode === 1) {
        setGenderList(
          r.data.Data.list.map((el) => ({
            ItemContent: ArabicToPersian(el.Name),
            ItemValue: el.Id,
          }))
        );
      } else {
        SwalAlert('error', r.data.resMessage, false);
      }
    });
  }, []);

  return genderList;
};

export default useGetGenderList;
