import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import { Post } from 'Xhr/Xhr';
import { env } from 'env';

export const SubmitUserInformation = async (data) => {
 
  return Post(
    'POST',
    `${process.env.REACT_APP_API_URL_API_UPDATE_USER_PROFILE}`,
    true,
    {
      ...data,
    }
  ).then((response) => {
    if (response.data.resCode === 1) {
      SwalAlert('success', response.data.resMessage, false);
    } else {
      SwalAlert('error', response.data.resMessage, false);
    }
  });
};
