import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import React, { useEffect, useState } from 'react'
import { ArabicToPersian } from 'utils/ConvertToPersianTextFromArabic';
import { Post } from 'Xhr/Xhr';

const useGetTaholList = () => {
   const[taholList,setTaholList] = useState([]);

   useEffect(()=>{
    Post('Post',`${process.env.REACT_APP_API_URL_API_GET_MARTIAL_STATUS_LIST}`,{})
    .then((response)=>{
        if(response.data.resCode === 1){
            setTaholList(response.data.Data.list.map((el)=>({
                ItemContent:ArabicToPersian(el.Name),
                ItemValue:el.Id
            })))
        }else{
           SwalAlert('error',response.data.resMessage,false);
        }
    })
   },[]);

   return taholList;
}

export default useGetTaholList