import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import Cookies from 'js-cookie';
import { Post } from 'Xhr/Xhr';
import { env } from 'env';

export const GetUserInformation = async (
  setUserInformation
) => {
  const Id =
    Cookies.get('user') &&
    JSON.parse(Cookies.get('user'))?.Id;
  return Post(
    'POST',
    `${process.env.REACT_APP_API_URL_API_GET_USER_PROFILE_INFO}`,
    true,
    {
      Id,
    }
  ).then((response) => {
    if (response.data.resCode === 1) {
      setUserInformation(response.data.Data);
    } else {
      SwalAlert('error', response.data.resMessage, false);
    }
  });
};
