export const formatData = (cityList,genderList,data,user_cookie,userId,HandleFormSubmit,userInformation)=>{
    const cityId = cityList?.filter(
        (fl) => fl.label === data.nameCityTavalod
      )[0]?.id;
      const genderId = genderList?.filter(
        (fl) => fl.ItemValue === data.IdGender
      )[0]?.ItemValue;

      data.idCityTavalod = cityId;
      data.idCitySokonat = data.idCityTavalod;

      data.FullName =`${data.FirstName}Â ${data.LastName}`;
      data.Id = userId

      data.IdGender = genderId; 
      data.NationalCode = user_cookie.NationalCode;
      data.NameGender = genderList?.filter(
        (fl) => fl.ItemValue === data.IdGender
      )[0]?.ItemContent
      // HandleFormSubmit(data, userInformation);
}