export const CompleteUserProfileDefaultValues = (userInformation) => {
  const completeProfileDefaultValues = {
    FirstName: userInformation?.FirstName,
    LastName: userInformation?.LastName,
    FatherName: userInformation?.FatherName,
    BirthDate: userInformation?.BirthDate,
    NationalCode: userInformation?.NationalCode,
    Address: userInformation?.Address,
    Telephon: userInformation?.Telephon,
    Mobile: userInformation?.Mobile,
    FullName: userInformation?.FullName,
    Email: userInformation?.Email,
    Id: userInformation?.Id,
    IdGender: userInformation?.IdGender,
    NameGender: userInformation?.NameGender,
    idCitySokonat: userInformation?.idCitySokonat,
    idCityTavalod: userInformation?.idCityTavalod,
  };
  return completeProfileDefaultValues;
};
